@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

body {
  font-family: "Raleway", sans-serif;
  font-size: 14px;
}

h1,
.h1,
h2,
.h2 {
  font-weight: 800;
}

.app_body {
  position: relative;
  height: 100vh;
  width: 100%;
  padding: 60px 0px;
  overflow-y: auto;
  background-color: #FFFEF2;
}

.brand_logo{
  margin-bottom: 30px;
}

.section_titel {
  margin-bottom: 30px;
}

.count_span {
  margin-bottom: 30px;
  font-weight: 600;
}

.count_span span {
  color: #005B41;
  font-weight: 600;
  font-size: 18px;
}

.drop_box {
  border: 2px dashed #cccccc;
  padding: 60px;
  text-align: center;
  cursor: pointer;
  border-radius: 15px;
  margin-bottom: 20px;
  background: #FFF;
}

.drop_box {
  font-size: 18px;
  font-weight: 500;
  color: #000;
}

.drop_box:hover{
  border-color: #000;
}

.snap_btns {
  margin-bottom: 30px;
}

.snap_btn_bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #ffffff;
  text-align: center;
  padding: 20px 0px;
  box-shadow: 0px -7px 18px rgba(1, 11, 60, 0.1);
  z-index: 999;
}

.snap_btn_group .btn {
  width: 100%;
}

.btn {
  padding: 10px 40px;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  white-space: nowrap;
  border-radius: 25px;
  margin-bottom: 20px;
}

.btn:focus{
  box-shadow: none;
}

.snap_converted_item {
  width: 100%;
  height: 250px;
  position: relative;
  margin-bottom: 20px;
}

.snap_converted_item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.snap_converted_item .btn_icon {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  padding: 0;
  color: #fff;
  background: #198754;
  border: 1px solid #198754;
}

.snap_converted_item .btn_icon:hover{
  background: #fff;
  border-color: #198754;
  color: #198754;
}

.snap_converted{
  margin-bottom: 30px;
}

.btn-primary{
  background: #5454C5;
  border: 1px solid #5454C5;
  color: #FFF;
}

.btn-primary:hover{
  background: #FFF;
  color: #5454C5;
  border-color: #5454C5;
}

.btn-primary:focus, .btn-primary:focus-within,
.btn-warning:focus, .btn-warning:focus-within,
.btn-info:focus, .btn-info:focus-within,
.btn-dark:focus, .btn-dark:focus-within{
  box-shadow: none !important;
}

.btn-warning{
  background: #03506F;
  border: 1px solid #03506F;
  color: #FFF;
}

.btn-warning:hover{
  background: #FFF;
  color: #03506F;
  border-color: #03506F;
}

.btn-info{
  background: #ECB365;
  border: 1px solid #ECB365;
  color: #FFF;
}

.btn-info:hover{
  background: #FFF;
  color: #ECB365;
  border-color: #ECB365;
}

.btn-dark{
  background: #000;
  border: 1px solid #000;
}

.btn-dark:hover{
  background: #FFF;
  color: #000;
  border-color: #000;
}

.btn-success{
  background: #005B41;
  border: 1px solid #005B41;
}

.btn-success:hover{
  background: #FFF;
  color: #005B41;
  border-color: #005B41;
}